import React, { useState, useEffect, useCallback } from "react";
import { withRouter } from "react-router-dom";
import axios from "axios";
import { Form, Button, Row, Col, Table } from "react-bootstrap";
import Snackbar from "@mui/material/Snackbar";
import MuiAlert from "@mui/material/Alert";
import XLSX from "xlsx";
import FormData from "form-data";
import Box from "@mui/material/Box";
import DialogContent from "@mui/material/DialogContent";
import CircularProgress from "@mui/material/CircularProgress";
import Dialog from "@mui/material/Dialog";

const Alert = React.forwardRef(function Alert(props, ref) {
  return <MuiAlert elevation={6} ref={ref} variant="filled" {...props} />;
});

let selectedMobitelOfficer = null;
let selectedSubcon = null;

function SubconAllocationBody(props) {
  // console.log(props.location.data);
  let [users, setUsers] = useState([]);
  let [subconUsers, setSubconUsers] = useState([]);
  let [companies, setCompanies] = useState([]);
  let [approvalPath, setApprovalPath] = useState([]);
  let [verificationOne, setVerificationOne] = useState([]);
  let [verificationTwo, setVerificationTwo] = useState([]);
  let [approval, setApproval] = useState([]);
  let [msg, setMsg] = useState("");
  let [variant, setVariant] = useState("");
  let [projects, setProjects] = useState([]);
  let [selectedFiles, setSelectedFiles] = useState([]);
  const [isBoqRequired, setIsBoqRequired] = useState(true); // Default value is 'true'
  const axiosInstance = axios.create({
    baseURL: process.env.REACT_APP_BACKEND_URL,
  });

  let [form, setForm] = useState({});
  let [tasks, setTasks] = useState([]);
  let [errors, setErrors] = useState({});
  let [open, setOpen] = useState(false);
  let [openDialog, setOpenDialog] = useState(false);

  let setField = (field, value) => {
    setForm({
      ...form,
      [field]: value,
    });
    if (!!errors[field])
      setErrors({
        ...errors,
        [field]: null,
      });
  };

  // Get Assign Officer and Subcon Names to the Task History
  const temp1 = users.filter(
    (obj) => obj._id === form.assignedMobitelOfficer
  )[0];
  const temp2 = companies.filter((obj) => obj._id === form.assignedSubcon)[0];

  if ((temp1, temp2)) {
    selectedMobitelOfficer = temp1.name;
    selectedSubcon = temp2.companyName;
  }

  const findFormErrors = () => {
    const {
      plannedTaskStartDate,
      targetTaskComDate,
      taskAssignedDiv,
      assignedMobitelOfficer,
      workScope,
      assignedSubcon,
      verificationOneId,
      approvalId,
    } = form;
    const newErrors = {};
    // name errors
    if (!plannedTaskStartDate || plannedTaskStartDate === "")
      newErrors.plannedTaskStartDate = "cannot be blank!";
    else if (new Date(plannedTaskStartDate).getTime() <= new Date().getTime())
      newErrors.plannedTaskStartDate = "Select a correct date!";
    if (!targetTaskComDate || targetTaskComDate === "")
      newErrors.targetTaskComDate = "cannot be blank!";
    else if (
      new Date(targetTaskComDate).getTime() <= new Date().getTime() ||
      new Date(targetTaskComDate).getTime() <=
        new Date(plannedTaskStartDate).getTime()
    )
      newErrors.targetTaskComDate = "Select a correct date!";
    if (
      !taskAssignedDiv ||
      taskAssignedDiv === "" ||
      taskAssignedDiv === "Choose..."
    )
      newErrors.taskAssignedDiv = "Choose a division!";
    if (
      !assignedMobitelOfficer ||
      assignedMobitelOfficer === "" ||
      assignedMobitelOfficer === "Choose..."
    )
      newErrors.assignedMobitelOfficer = "Choose a Mobitel Officer!";
    if (!workScope || workScope === "")
      newErrors.workScope = "cannot be blank!";
    if (!assignedSubcon || assignedSubcon === "")
      newErrors.assignedSubcon = "Choose a Subcontractor!";
    // if (!verificationOneId || verificationOneId === "")
    //   newErrors.verificationOneId = "Choose a officer!";
    // if (!approvalId || approvalId === "")
    //   newErrors.approvalId = "Choose a officer!";

    return newErrors;
  };

  useEffect(() => {
    axiosInstance
      .get("projonline/users", {
        headers: {
          token:
            "Bearer " + JSON.parse(localStorage.getItem("user")).accessToken,
        },
      })
      .then((res) => {
        // console.log(res.data.users)
        setTasks(props.location.data);
        setSubconUsers(
          res.data.users.filter((obj) => obj.company.companyName !== "Mobitel")
        );
        setUsers(
          res.data.users.filter((obj) => obj["userCanAssignForTask"] === true)
        );

        setCompanies(
          res.data.companys.filter((obj) => obj.companyName !== "Mobitel")
        );
        setApprovalPath(res.data.users);

        setVerificationOne([{ _id: "", name: "Choose..." }]);
        setVerificationTwo([{ _id: "", name: "Choose..." }]);
        setApproval([{ _id: "", name: "Choose..." }]);
        setForm({
          taskAssignedDiv: props.location.data[0].taskAssignedDiv,
        });

        //get available project templates
        setProjects(res.data.projects);
        // console.log("---");
        // console.log(res.data.projects);
        // console.log("---");
        return;
      })
      .catch((err) => {
        console.log(err);
      });
  }, []);

  const downloadHandover = (e) => {
    //Create Downloadable excel file
    var workbook = XLSX.utils.book_new();
    var aoa_projects = [];

    //get selected projects
    var selectedProjects = [
      ...new Set(props.location.data.map((ele) => ele.projectID)),
    ];
    var selectedProjectsHeaderData = [];

    for (var i = 0; i < projects.length; i++) {
      for (var j = 0; j < selectedProjects.length; j++) {
        // console.log(projects[i])
        if (selectedProjects[j] === projects[i].projectID) {
          var worksheetData = [];

          worksheetData = props.location.data
            .filter((ele) => ele.projectID === selectedProjects[j])
            .map((ele) => ele.properties);
          worksheetData.unshift(projects[i].properties);
          // console.log(worksheetData)
          var ws = XLSX.utils.aoa_to_sheet(worksheetData);
          XLSX.utils.book_append_sheet(
            workbook,
            ws,
            projects[i].subProject + "_" + projects[i].projectID
          );
        }
      }
    }
    XLSX.writeFile(workbook, "Handover details.xlsx");
    // console.log(workbook)
  };

  const smsFetch = async (mobileNo, msg) => {
    // console.log(users.filter((obj) => (obj._id === form.assignedMobitelOfficer))[0].mobileNo)
    const url =
      "https://msmsenterpriseapi.mobitel.lk/EnterpriseSMSV3/esmsproxyURL.php";
    const Body = {
      username: "smsdigisvc_sci",
      password: "Sci@#25",
      from: "Mobitel-PMS",
      to: "0" + mobileNo.toString(),
      text: "New " + tasks.length + msg,
      mesageType: 0,
    };
    //console.log(JSON.parse(localStorage.getItem("user")))
    const requestOptions = {
      method: "POST",
      body: JSON.stringify(Body),
      mode: "no-cors",
    };
    const response = await fetch(url, requestOptions);
    // console.log(response)
  };

  const onSubmit = (e) => {
    e.preventDefault();

    const newErrors = findFormErrors();
    // Conditional logic:
    if (Object.keys(newErrors).length > 0) {
      // We got errors!
      setErrors(newErrors);
    } else {
      var uploadfiles = new FormData();

      for (var x = 0; x < selectedFiles.length; x++) {
        uploadfiles.append("files", selectedFiles[x]);
      }

      var taskApprovalPath = [
        {
          approvalLevel: "Verification Level 0",
          assignedOfficer: form.assignedMobitelOfficer,
        },
        {
          approvalLevel: "Verification Level 1",
          assignedOfficer: form.verificationOneId,
        },
        {
          approvalLevel: "Verification Level 2",
          assignedOfficer: form.verificationTwoId,
        },
        { approvalLevel: "Final approval", assignedOfficer: form.approvalId },
      ].filter((element) => element.assignedOfficer != "");

      const taskObject = {
        plannedTaskStartDate: form.plannedTaskStartDate,
        targetTaskComDate: form.targetTaskComDate,
        taskAssignedDiv: form.taskAssignedDiv,
        assignedMobitelOfficer: form.assignedMobitelOfficer,
        workScope: form.workScope,
        assignedSubcon: form.assignedSubcon,
        taskStatus: "Subcon allocated",
        approvalPath: taskApprovalPath,
        boqprojectrequirement: isBoqRequired,
      };

      setOpenDialog(true);
      axiosInstance
        .post("projonline/upload", uploadfiles, {
          headers: {
            token:
              "Bearer " + JSON.parse(localStorage.getItem("user")).accessToken,
            "Content-type": "multipart/form-data",
          },
        })
        .then((res1) => {
          // console.log(res1)
          taskObject.files = res1.data;
          //props.history.push({pathname:'/initiateSubconAllocationBody', data: {msg,variant}})
          axiosInstance
            .post(
              "projonline/update",
              {
                tasks,
                taskObject,
                taskHistory: {
                  taskStatus: taskObject.taskStatus,
                  comment:
                    "Officer: " +
                    selectedMobitelOfficer +
                    " || " +
                    "Subcon: " +
                    selectedSubcon,
                },
              },
              {
                headers: {
                  token:
                    "Bearer " +
                    JSON.parse(localStorage.getItem("user")).accessToken,
                },
              }
            )
            .then((res) => {
              // console.log(res.statusText)
              //setOpenDialog(true)
              smsFetch(
                users.filter(
                  (obj) => obj._id === form.assignedMobitelOfficer
                )[0].mobileNo,
                " task/s were assigned to you."
              );
              subconUsers.forEach((ele) => {
                if (ele.company._id === form.assignedSubcon) {
                  smsFetch(
                    ele.mobileNo,
                    " task/s were handedover to " + ele.company.companyName
                  );
                }
              });

              setTimeout(() => {
                setMsg(res.data.msg);
                setVariant(res.data.variant);
                setOpen(true);
              }, 2000);

              setTimeout(() => {
                setOpenDialog(false);
                setOpen(false);
                setVerificationOne([{ _id: "", name: "Choose..." }]);
                setVerificationTwo([{ _id: "", name: "Choose..." }]);
                setApproval([{ _id: "", name: "Choose..." }]);
                setTasks([]);
                setForm({
                  plannedTaskStartDate: "",
                  targetTaskComDate: "",
                  taskAssignedDiv: "",
                  verificationOneId: "",
                  verificationTwoId: "",
                  approvalId: "",
                  assignedMobitelOfficer: "",
                  workScope: "",
                  assignedSubcon: "",
                  approvalType: "Default",
                  isBoqRequired: isBoqRequired,
                });
                props.history.push({
                  pathname: "/initiateSubconAllocationBody",
                });
              }, 5000);
            })
            .catch((error) => {
              console.log(error);
            });
        })
        .catch((error) => {
          console.log(error);
        });
    }
  };

  //Upload Files
  const onChangeFiles = (e) => {
    setSelectedFiles(e.target.files);
    // console.log(e.target.files)
  };

  const handleClose = (event, reason) => {
    if (reason === "clickaway") {
      return;
    }

    setOpen(false);
  };

  //Assigned Mobitel Officer Handler
  const onChangeAssignedMobitelOfficer = (e) => {
    if (e.target.value === "Choose...") {
      setField("assignedMobitelOfficer", e.target.value);
      setVerificationOne([{ _id: "", name: "Choose..." }]);
      setVerificationTwo([{ _id: "", name: "Choose..." }]);
      setApproval([{ _id: "", name: "Choose..." }]);
    }

    for (let i = 0; i < approvalPath.length; i++) {
      if (approvalPath[i]._id === e.target.value) {
        // console.log(e.target.value)
        // console.log(form)

        switch (approvalPath[i].approvalPath.length) {
          case 0:
            setVerificationOne([{ _id: "", name: "Choose..." }]);
            setVerificationTwo([{ _id: "", name: "Choose..." }]);
            setApproval([{ _id: "", name: "Choose..." }]);
            setForm({
              ...form,
              verificationOneId: "",
              verificationTwoId: "",
              approvalId: "",
              assignedMobitelOfficer: e.target.value,
              approvalType: "Default",
            });
            if (!!errors["assignedMobitelOfficer"])
              setErrors({
                ...errors,
                assignedMobitelOfficer: null,
              });
            break;
          case 1:
            setVerificationOne([
              {
                _id: approvalPath[i].approvalPath[0]._id,
                name: approvalPath[i].approvalPath[0].name,
              },
            ]);
            setField("verificationOneId", approvalPath[i].approvalPath[0]._id);
            setVerificationTwo([{ _id: "", name: "Choose..." }]);
            setApproval([{ _id: "", name: "Choose..." }]);
            setForm({
              ...form,
              verificationOneId: approvalPath[i].approvalPath[0]._id,
              verificationTwoId: "",
              approvalId: "",
              assignedMobitelOfficer: e.target.value,
              approvalType: "Default",
            });
            if (!!errors["assignedMobitelOfficer"])
              setErrors({
                ...errors,
                assignedMobitelOfficer: null,
              });

            break;
          case 2:
            setVerificationOne([
              {
                _id: approvalPath[i].approvalPath[0]._id,
                name: approvalPath[i].approvalPath[0].name,
              },
            ]);
            setVerificationTwo([{ _id: "", name: "Choose...." }]);
            setApproval([
              {
                _id: approvalPath[i].approvalPath[1]._id,
                name: approvalPath[i].approvalPath[1].name,
              },
            ]);
            setForm({
              ...form,
              verificationOneId: approvalPath[i].approvalPath[0]._id,
              verificationTwoId: "",
              approvalId: approvalPath[i].approvalPath[1]._id,
              assignedMobitelOfficer: e.target.value,
              approvalType: "Default",
            });
            if (!!errors["assignedMobitelOfficer"])
              setErrors({
                ...errors,
                assignedMobitelOfficer: null,
              });
            break;
          case 3:
            setVerificationOne([
              {
                _id: approvalPath[i].approvalPath[0]._id,
                name: approvalPath[i].approvalPath[0].name,
              },
            ]);
            setVerificationTwo([
              {
                _id: approvalPath[i].approvalPath[1]._id,
                name: approvalPath[i].approvalPath[1].name,
              },
            ]);
            setApproval([
              {
                _id: approvalPath[i].approvalPath[2]._id,
                name: approvalPath[i].approvalPath[2].name,
              },
            ]);
            setForm({
              ...form,
              verificationOneId: approvalPath[i].approvalPath[0]._id,
              verificationTwoId: approvalPath[i].approvalPath[1]._id,
              approvalId: approvalPath[i].approvalPath[2]._id,
              assignedMobitelOfficer: e.target.value,
              approvalType: "Default",
            });
            if (!!errors["assignedMobitelOfficer"])
              setErrors({
                ...errors,
                assignedMobitelOfficer: null,
              });

            break;
        }
      }
    }
  };

  //Approval Path Handler
  const onChangeApprovalType = (e) => {
    // console.log(e.target.value)
    // console.log(form.approvalType)

    if (e.target.value === "New") {
      let mobitelUsers = approvalPath.filter(
        (obj) => obj.company.companyName === "Mobitel"
      );
      setVerificationOne(mobitelUsers);
      setVerificationTwo(mobitelUsers);
      setApproval(mobitelUsers);
      setForm({
        ...form,
        verificationOneId: mobitelUsers[0]._id,
        verificationTwoId: mobitelUsers[0]._id,
        approvalId: mobitelUsers[0]._id,
        approvalType: e.target.value,
      });
      if (!!errors["approvalType"])
        setErrors({
          ...errors,
          assignedMobitelOfficer: null,
        });
    } else if (e.target.value === "Default") {
      for (let i = 0; i < approvalPath.length; i++) {
        if (approvalPath[i]._id === form.assignedMobitelOfficer) {
          switch (approvalPath[i].approvalPath.length) {
            case 0:
              setVerificationOne([{ _id: "", name: "Choose..." }]);
              setVerificationTwo([{ _id: "", name: "Choose..." }]);
              setApproval([{ _id: "", name: "Choose..." }]);
              setForm({
                ...form,
                verificationOneId: "",
                verificationTwoId: "",
                approvalId: "",
                approvalType: e.target.value,
              });
              if (!!errors["approvalType"])
                setErrors({
                  ...errors,
                  approvalType: null,
                });
              break;
            case 1:
              setVerificationOne([
                {
                  _id: approvalPath[i].approvalPath[0]._id,
                  name: approvalPath[i].approvalPath[0].name,
                },
              ]);
              setField(
                "verificationOneId",
                approvalPath[i].approvalPath[0]._id
              );
              setVerificationTwo([{ _id: "", name: "Choose..." }]);
              setApproval([{ _id: "", name: "Choose..." }]);
              setForm({
                ...form,
                verificationOneId: approvalPath[i].approvalPath[0]._id,
                verificationTwoId: "",
                approvalId: "",
                approvalType: e.target.value,
              });
              if (!!errors["approvalType"])
                setErrors({
                  ...errors,
                  approvalType: null,
                });

              break;
            case 2:
              setVerificationOne([
                {
                  _id: approvalPath[i].approvalPath[0]._id,
                  name: approvalPath[i].approvalPath[0].name,
                },
              ]);
              setVerificationTwo([{ _id: "", name: "Choose...." }]);
              setApproval([
                {
                  _id: approvalPath[i].approvalPath[1]._id,
                  name: approvalPath[i].approvalPath[1].name,
                },
              ]);
              setForm({
                ...form,
                verificationOneId: approvalPath[i].approvalPath[0]._id,
                verificationTwoId: "",
                approvalId: approvalPath[i].approvalPath[1]._id,
                approvalType: e.target.value,
              });
              if (!!errors["approvalType"])
                setErrors({
                  ...errors,
                  approvalType: null,
                });
              break;
            case 3:
              setVerificationOne([
                {
                  _id: approvalPath[i].approvalPath[0]._id,
                  name: approvalPath[i].approvalPath[0].name,
                },
              ]);
              setVerificationTwo([
                {
                  _id: approvalPath[i].approvalPath[1]._id,
                  name: approvalPath[i].approvalPath[1].name,
                },
              ]);
              setApproval([
                {
                  _id: approvalPath[i].approvalPath[2]._id,
                  name: approvalPath[i].approvalPath[2].name,
                },
              ]);
              setForm({
                ...form,
                verificationOneId: approvalPath[i].approvalPath[0]._id,
                verificationTwoId: approvalPath[i].approvalPath[1]._id,
                approvalId: approvalPath[i].approvalPath[2]._id,
                approvalType: e.target.value,
              });
              if (!!errors["approvalType"])
                setErrors({
                  ...errors,
                  approvalType: null,
                });

              break;
          }
        }
      }
    }
  };

  return (
    <div>
      {/* Content Wrapper. Contains page content */}
      <div className="content-wrapper">
        {/* Content Header (Page header) */}
        <div className="content-header">
          <section className="content-header">
            <div className="container-fluid">
              <div className="row mb-2">
                <div className="col-sm-6">
                  <h1>Initiate Sub-Contractor Allocation</h1>
                </div>
              </div>
            </div>
            {/* /.container-fluid */}
          </section>
        </div>
        {/* /.content-header */}
        {/* Main content */}
        <section className="content">
          <div className="container-fluid">
            <form onSubmit={onSubmit} enctype="multipart/form-data">
              <div>
                <Row>
                  <Table striped bordered hover size="sm">
                    <thead>
                      <tr>
                        <th>Task Ref</th>
                        <th>Site ID</th>
                        <th>Site Name</th>
                        <th>Task Assigned Div</th>
                      </tr>
                    </thead>
                    <tbody>
                      {tasks.map((item) => (
                        <tr>
                          <td>{item.taskRef}</td>
                          <td>{item.siteID}</td>
                          <td>{item.siteName}</td>
                          <td>{item.taskAssignedDiv}</td>
                        </tr>
                      ))}
                    </tbody>
                  </Table>
                </Row>
              </div>

              <Row className="mb-3">
                <Form.Group as={Col}>
                  <Form.Label>Planned Work Commencement Date</Form.Label>
                  <Form.Control
                    onChange={(e) =>
                      setField("plannedTaskStartDate", e.target.value)
                    }
                    value={form.plannedTaskStartDate}
                    className="col-md-9"
                    type="date"
                    isInvalid={!!errors.plannedTaskStartDate}
                  />
                  <Form.Control.Feedback type="invalid">
                    {errors.plannedTaskStartDate}
                  </Form.Control.Feedback>
                </Form.Group>

                <Form.Group as={Col}>
                  <Form.Label>Planned Work Completion Date</Form.Label>
                  <Form.Control
                    onChange={(e) =>
                      setField("targetTaskComDate", e.target.value)
                    }
                    value={form.targetTaskComDate}
                    className="col-md-9"
                    type="date"
                    isInvalid={!!errors.targetTaskComDate}
                  />
                  <Form.Control.Feedback type="invalid">
                    {errors.targetTaskComDate}
                  </Form.Control.Feedback>
                </Form.Group>

                <Form.Group as={Col}></Form.Group>
              </Row>
              <Row>
                <Form.Group as={Col}>
                  <Form.Label>Task Assigned Division</Form.Label>
                  <Form.Control
                    onChange={(e) =>
                      setField("taskAssignedDiv", e.target.value)
                    }
                    value={form.taskAssignedDiv}
                    as="select"
                    defaultValue="Choose..."
                    isInvalid={!!errors.taskAssignedDiv}
                  >
                    <option>Choose...</option>
                    <option>Project_Radio</option>
                    <option>Project_Core</option>
                    <option>Project_TX</option>
                    <option>Project_IBS</option>
                    <option>Project_WiFi</option>
                  </Form.Control>
                  <Form.Control.Feedback type="invalid">
                    {errors.taskAssignedDiv}
                  </Form.Control.Feedback>
                </Form.Group>

                <Form.Group as={Col}>
                  <Form.Label>Assigned Officer</Form.Label>
                  <Form.Control
                    onChange={onChangeAssignedMobitelOfficer}
                    as="select"
                    value={form.assignedMobitelOfficer}
                    defaultValue="Choose..."
                    isInvalid={!!errors.assignedMobitelOfficer}
                  >
                    <option>Choose...</option>
                    {users.map((opt) => (
                      <option value={opt._id}>{opt.name}</option>
                    ))}
                  </Form.Control>
                  <Form.Control.Feedback type="invalid">
                    {errors.assignedMobitelOfficer}
                  </Form.Control.Feedback>
                </Form.Group>

                <Form.Group as={Col}>
                  <Form.Label>Approval Path</Form.Label>
                  <Form.Control
                    onChange={onChangeApprovalType}
                    as="select"
                    value={form.approvalType}
                    defaultValue="Choose..."
                    isInvalid={!!errors.name}
                  >
                    <option>Default</option>
                    <option>New</option>
                  </Form.Control>
                </Form.Group>
              </Row>

              <Row>
                <Form.Group as={Col}>
                  <Form.Label>Verification by (Level 1)</Form.Label>
                  <Form.Control
                    as="select"
                    onChange={(e) =>
                      setField("verificationOneId", e.target.value)
                    }
                    value={form.verificationOneId}
                    defaultValue="Choose..."
                    isInvalid={!!errors.verificationOneId}
                  >
                    {verificationOne.map((opt) => (
                      <option value={opt._id}>{opt.name}</option>
                    ))}
                  </Form.Control>
                  <Form.Control.Feedback type="invalid">
                    {errors.verificationOneId}
                  </Form.Control.Feedback>
                </Form.Group>

                <Form.Group as={Col}>
                  <Form.Label>Verification by (Level 2)</Form.Label>
                  <Form.Control
                    as="select"
                    onChange={(e) =>
                      setField("verificationTwoId", e.target.value)
                    }
                    value={form.verificationTwoId}
                    defaultValue="Choose..."
                  >
                    {verificationTwo.map((opt) => (
                      <option value={opt._id}>{opt.name}</option>
                    ))}
                  </Form.Control>
                </Form.Group>

                <Form.Group as={Col}>
                  <Form.Label>Approval by</Form.Label>
                  <Form.Control
                    as="select"
                    onChange={(e) => setField("approvalId", e.target.value)}
                    value={form.approvalId}
                    defaultValue="Choose..."
                    isInvalid={!!errors.approvalId}
                  >
                    {approval.map((opt) => (
                      <option value={opt._id}>{opt.name}</option>
                    ))}
                  </Form.Control>
                  <Form.Control.Feedback type="invalid">
                    {errors.approvalId}
                  </Form.Control.Feedback>
                </Form.Group>
              </Row>

              <Row>
                <Form.Group as={Col}>
                  <Form.Label>High level work scope</Form.Label>
                  <Form.Control
                    onChange={(e) => setField("workScope", e.target.value)}
                    value={form.workScope}
                    as="textarea"
                    rows={2}
                    placeholder=""
                    isInvalid={!!errors.workScope}
                  />
                  <Form.Control.Feedback type="invalid">
                    {errors.workScope}
                  </Form.Control.Feedback>
                </Form.Group>
              </Row>

              <Row className="mb-3">
                <Form.Group as={Col}>
                  <Form.Label>Assigned subcontractor</Form.Label>
                  <Form.Control
                    onChange={(e) => setField("assignedSubcon", e.target.value)}
                    value={form.assignedSubcon}
                    as="select"
                    defaultValue="Choose..."
                    isInvalid={!!errors.assignedSubcon}
                  >
                    <option>Choose...</option>
                    {companies.map((opt) => (
                      <option value={opt._id}>{opt.companyName}</option>
                    ))}
                  </Form.Control>
                  <Form.Control.Feedback type="invalid">
                    {errors.assignedSubcon}
                  </Form.Control.Feedback>
                </Form.Group>

                <Form.Group as={Col}>
                  <Form.Label>Handover Details</Form.Label>
                  <br />
                  <Button
                    onClick={downloadHandover}
                    variant="primary"
                    type="button"
                  >
                    Click to Download
                  </Button>
                </Form.Group>

                <Form.Group as={Col}>
                  <Form.Label> Boq project requirement</Form.Label>
                  <Form.Check
                    onChange={(e) => setIsBoqRequired(!isBoqRequired)}
                    value={isBoqRequired}
                    checked={isBoqRequired}
                    className="col-md-9"
                    id="custom-switch-3"
                    type="switch"
                  />
                </Form.Group>

                <Form.Group as={Col}>
                  <Form.Label>Upload Supporting Documents</Form.Label>
                  <br />
                  <Form.Control
                    type="file"
                    multiple
                    onChange={onChangeFiles}
                    size="lg"
                  />
                </Form.Group>
              </Row>

              <Button variant="primary" type="submit">
                Assign Subcontractor
              </Button>
            </form>
          </div>
          {/* /.container-fluid */}
        </section>
        {/* /.content */}
      </div>

      <Snackbar
        open={open}
        autoHideDuration={6000}
        onClose={handleClose}
        anchorOrigin={{ vertical: "top", horizontal: "center" }}
      >
        <Alert onClose={handleClose} severity={variant} sx={{ width: "100%" }}>
          {msg}
        </Alert>
      </Snackbar>

      <Dialog
        open={openDialog}
        onClose={handleClose}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <DialogContent>
          <Box sx={{ display: "flex" }}>
            <CircularProgress />
          </Box>
        </DialogContent>
      </Dialog>
    </div>
  );
}

export default withRouter(SubconAllocationBody);
